@mixin left($l) {
  left: $l;
}
@mixin right($r) {
  right: $r;
}

.slider-list:not(.tns-slider) + .slider-controls {
	visibility: hidden; 
	pointer-events: none; 
}
.slider-controls {
	button {
		position: absolute;
		top: 50%;
		width: 32px;
		height: 32px;
		margin-top: -16px;
		border-radius: var(--border-radius);
		cursor: pointer;
		background: var(--miscellaneous-color);
		svg {
			display: flex;
			align-items: center;
			justify-content: center;
			margin: auto;
			width: 6px;
			path {
				fill: var(--primary-color);
			}
		}
		&:hover {
			opacity: 0.8;
		}
	}
	button[disabled] {
		opacity: 0;
		visibility: hidden;
	}
	.slider-prev-button {
		@include left(-10px);
	}
	.slider-next-button {
		@include right(-10px);
	}
	@media (min-width: 750px) {
		button {
			width: 40px;
			height: 40px;
			margin-top: -20px;
			svg {
				width: 9px;
			}
		}
	}
}
.tns-nav {
	position: absolute;
	left: 0;
	right: 0;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
	button {
		width: 5px;
		height: 5px;
		border-radius: 50%;
		margin: 0 5px;
		transition: all .25s ease-in-out;
		background: rgba(0,0,0,0.2);
		&:hover {
			background: #000;
		}
	}
	.tns-nav-active {
		background: #000;
	}
	@media (min-width: 750px) {
		button {
			width: 8px;
			height: 8px;
		}
		.tns-nav-active {
			width: 12px;
			height: 12px;
		}
	}
}
slider-component {
	position: relative;
    width: 100%;
    height: 100%;
    display: block;
}